import Vue from 'vue'
import Router from 'vue-router'
import About from '@/components/About.vue'
import PostsMain from '@/components/PostsMain.vue'
import ProjectsMain from '@/components/ProjectsMain.vue'
import NotFound from '@/components/NotFound.vue'
import Creation from '@/components/Posts/Creation.vue'

const routes = [
  { path: '/', component: About },
  { path: '/posts', component: PostsMain },
  { path: '/posts/creation', component: Creation },
  { path: '/projects', component: ProjectsMain },
  { path: '*', component: NotFound }
]

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes
})



